<script>
export default {
  name: 'Header',
  data() {
    return {
      showLoginModal: false,
      showRegisterModal: false,
      showUserDropdown: false,
      isLoggedIn: false,
      loggedInUser: null,
      loginForm: {
        username: '',
        password: ''
      },
      registerForm: {
        username: '',
        password: '',
        confirmPassword: '',
        email: ''
      }
    }
  },
  mounted() {
    // Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶Â∑≤ÁªèÁôªÂΩï
    this.checkLoginStatus()
  },
  methods: {
    // ÁôªÂΩïÂäüËÉΩ
    login() {
      // ‰ªélocalStorageËé∑ÂèñÁî®Êà∑Êï∞ÊçÆ
      const users = JSON.parse(localStorage.getItem('users')) || []
      const user = users.find(u => u.username === this.loginForm.username && u.password === this.loginForm.password)
      
      if (user) {
        // ÁôªÂΩïÊàêÂäü
        localStorage.setItem('loggedInUser', JSON.stringify(user))
        this.isLoggedIn = true
        this.loggedInUser = user
        this.showLoginModal = false
        this.loginForm = { username: '', password: '' }
        alert('ÁôªÂΩïÊàêÂäüÔºÅÊ¨¢ËøéÂõûÊù•Ôºå' + user.name)
      } else {
        // ÁôªÂΩïÂ§±Ë¥•
        alert('Áî®Êà∑ÂêçÊàñÂØÜÁ†ÅÈîôËØØÔºåËØ∑ÈáçËØï')
      }
    },
    
    // Ê≥®ÂÜåÂäüËÉΩ
    register() {
      if (this.registerForm.password !== this.registerForm.confirmPassword) {
        alert('‰∏§Ê¨°ËæìÂÖ•ÁöÑÂØÜÁ†Å‰∏ç‰∏ÄËá¥')
        return
      }
      
      // ‰ªélocalStorageËé∑ÂèñÁî®Êà∑Êï∞ÊçÆ
      const users = JSON.parse(localStorage.getItem('users')) || []
      
      // Ê£ÄÊü•Áî®Êà∑ÂêçÊòØÂê¶Â∑≤Â≠òÂú®
      if (users.some(u => u.username === this.registerForm.username)) {
        alert('Áî®Êà∑ÂêçÂ∑≤Â≠òÂú®ÔºåËØ∑ÈÄâÊã©ÂÖ∂‰ªñÁî®Êà∑Âêç')
        return
      }
      
      // ÂàõÂª∫Êñ∞Áî®Êà∑
      const newUser = {
        id: Date.now(),
        username: this.registerForm.username,
        name: this.registerForm.username, // ÈªòËÆ§‰ΩøÁî®Áî®Êà∑Âêç‰Ωú‰∏∫ÊòµÁß∞
        password: this.registerForm.password,
        email: this.registerForm.email,
        joinDate: new Date().toISOString(),
        points: 0,
        level: 1
      }
      
      // ‰øùÂ≠òÊñ∞Áî®Êà∑
      users.push(newUser)
      localStorage.setItem('users', JSON.stringify(users))
      
      // Ëá™Âä®ÁôªÂΩïÊñ∞Áî®Êà∑
      localStorage.setItem('loggedInUser', JSON.stringify(newUser))
      this.isLoggedIn = true
      this.loggedInUser = newUser
      this.showRegisterModal = false
      this.registerForm = { username: '', password: '', confirmPassword: '', email: '' }
      alert('Ê≥®ÂÜåÊàêÂäüÔºÅÊ¨¢ËøéÂä†ÂÖ•Á§æÂå∫ÂûÉÂúæÂàÜÁ±ªÁ≥ªÁªü')
    },
    
    // ÈÄÄÂá∫ÁôªÂΩï
    logout() {
      localStorage.removeItem('loggedInUser')
      this.isLoggedIn = false
      this.loggedInUser = null
      this.showUserDropdown = false
      alert('Â∑≤ÊàêÂäüÈÄÄÂá∫ÁôªÂΩï')
    },
    
    // Ê£ÄÊü•ÁôªÂΩïÁä∂ÊÄÅ
    checkLoginStatus() {
      const user = localStorage.getItem('loggedInUser')
      if (user) {
        this.isLoggedIn = true
        this.loggedInUser = JSON.parse(user)
      }
    },
    
    // ÂàáÊç¢Âà∞Ê≥®ÂÜåË°®Âçï
    switchToRegister() {
      this.showLoginModal = false
      this.showRegisterModal = true
    },
    
    // ÂàáÊç¢Âà∞ÁôªÂΩïË°®Âçï
    switchToLogin() {
      this.showRegisterModal = false
      this.showLoginModal = true
    },
    
    // ÊòæÁ§∫Áî®Êà∑ËµÑÊñô
    showUserProfile() {
      this.showUserDropdown = false
      alert(`Êü•ÁúãÁî®Êà∑ËµÑÊñôÔºö${this.loggedInUser.name}`)
      // ÂÆûÈôÖÂ∫îÁî®‰∏≠ÂèØ‰ª•Ë∑≥ËΩ¨Âà∞Áî®Êà∑ËµÑÊñôÈ°µÈù¢
    },
    
    // ÊòæÁ§∫Áî®Êà∑ËÆæÁΩÆ
    showUserSettings() {
      this.showUserDropdown = false
      alert('ÊâìÂºÄÁî®Êà∑ËÆæÁΩÆ')
      // ÂÆûÈôÖÂ∫îÁî®‰∏≠ÂèØ‰ª•ÊâìÂºÄËÆæÁΩÆÈù¢Êùø
    }
  }
}
