<template>
  <div class="feature-card" @click="$emit('click')">
    <div class="feature-header">
      <div class="feature-icon">{{ icon }}</div>
      <div class="feature-title">{{ title }}</div>
    </div>
    <div class="feature-content">{{ content }}</div>
  </div>
</template>
<script>
export default {
  name: 'FeatureCard',
  props: {
    icon: {
      type: String,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    content: {
      type: String,
      required: true
    }
  },
  emits: ['click']
}
</script>